.cooperation {
    display: flex;
    margin: 20px 0 15px;
    flex-wrap: wrap;
    justify-content: center;

    .col-xs-1 {
        padding: 5px;
    }

    .cooperation__item {
        flex: 0 0 10%;

        @media (max-width: 991px) {
            & {
                flex: 0 0 20%;
            }
        }

        @media (max-width:600px) {
            & {
                flex: 0 0 30%;
            }
        }

        .cooperation__item__img {
            max-width: 100%;
        }
    }
}

.box {
    margin: 10px 0 20px;
    clear: both;
    overflow: hidden;

    .box__item {
        background-color: #dcdcd9;
        border-radius: 3px;
        padding: 20px;

        @media (max-width: 991px) {
            margin-top: 20px;
        }

        .box__item__title {
            margin: 0;
            font-family: AMitra1;
            text-align: center;
            color: #04096b;
            font-size: 26px;
            font-weight: 500;
        }

        .box__item__subtitle {
            margin: 5px 0 0 0;
            font-family: AMitra1;
            text-align: center;
            color: #21a1b6;
            font-size: 20px;
            font-weight: 500;
        }

        .box__item__desc {
            margin-top: 18px;
            font-family: AMitra1;
            text-align: center;
            color: #463f3c;
            font-size: 18px;
            font-weight: 500;
            min-height: 150px;

            @media (max-width: 1200px) and (min-width:992px) {
                min-height: 200px;
            }

            @media (max-width:767px) {
                min-height: auto;
            }
        }

        .box__item__link {
            margin-top: 20px;
            font-family: AMitra1;
            text-align: center;
            color: #463f3c;
            font-size: 18px;
            font-weight: 500;
            display: block;
        }

        .box__item__btn {
            color: #fff;
            display: block;
            margin: 20px auto 0;
            font-family: AMitra1;
            outline: 0 !important;
            box-shadow: 0 !important;
            width: 260px;

            @media (max-width: 991px) and (min-width:768px) {
                width: auto;
            }
        }
    }
}

