.news-ticker {
    padding: 10px 0;
    //background-color: #014b7c;
    background-color: #00a8b0;
    display: flex;
    align-items: center;

    .vticker {
        ul {
            list-style-type: none;
            padding: 0;


            li {
                a {
                    color: #fff;
                    font-family: BNazanin !important;
                    font-size: 18px;
                }
            }
        }
    }

    .vticker-prev,
    .vticker-next {
        color: #fff;
        cursor: pointer;
        font-size: 18px;
    }

    .news-ticker__title {
        //color: #81c341;
        color: #014b7c;
        font-family: MyriadArabic;
        font-size: 21px;
    }

    .news-ticker__link {
        color: #fff;
        font-family: MyriadArabic;
        font-size: 21px;
        text-shadow: 0px 0px 5px #fff;
    }
}

