.mainMenu {
    .dropdown-menu {
        background-color: #5D8BB6;
        li {
            border-bottom: 1px solid #ccc;
            &:last-of-type {
                border-bottom: none !important;
            }
            a {
                font-family: 'MehdiHeydari';
                padding: 6px 14px;
                color: #fff;
    
                &:hover {
                    background-color: #014B7C;
                    color: #fff;
                }
            }
        }
    }

    
}

.mainMenu .mainMenu__right__item__link:hover, .mainMenu .mainMenu__left__item__link:hover {
    color: #04096B !important;
}

@media (max-width: 1200px) and (min-width: 768px) {
    .mainMenu__right, .mainMenu__left {
        float: none !important;
        display: table;
        margin: 0 auto;
    }
}

@media (min-width: 768px) {
    .navbar-right .dropdown-menu {
        left: -40px;
        right: auto;
    }
